function toTreeData(data,pid){
    var result = [] , temp;
    for(var i in data){
        if(data[i].parentid==pid){
            result.push(data[i]);
            data[i].children = [];
            temp = toTreeData(data,data[i]._id);
            if(temp.length>0){
                data[i].children=temp;
                //console.log(i, data[i].children)
            }
        }
    }
    return result;
}
module.exports = toTreeData



